 <template>
   <div class="login">
     <el-card class="box-card">
       <template #header>
         <div class="card-header">
           <span>用户登录系统</span>
         </div>
       </template>
       <el-form label-width="80px" :model="form" ref="form" :rules="rules">
         <el-form-item label="用户名:" prop="username">
           <el-input v-model="form.username"></el-input>
         </el-form-item>
         <el-form-item label="密码:" prop="password">
           <el-input type="password" v-model="form.password"></el-input>
         </el-form-item>
         <el-form-item>
           <el-button type="primary" @click="login('form')">登录</el-button>
         </el-form-item>
         <el-form-item>
           <el-button type="success" @click="anonymousLogin()">游客登录</el-button>
         </el-form-item>
       </el-form>
     </el-card>
   </div>
  </template>


<script>
import router from "@/router";
import {nameRule,passRule} from '@/utiils/vaildate'
export default {
  data(){
    return{
      form:{
        username:'',
        password:'',
      },
      rules:{
        username:{validator:nameRule,trigger:'blur'},
        password:{validator:passRule,trigger:'blur'}
      }
    }
  },
  methods:{
    login(form){
      this.$refs[form].validate((res)=>{
        if(res){
          this.$message({message:"用户:"+form.username+"  "+"不存在,请使用游客登录",type:'warning'})
        }else {
          this.$message({message:"如果没有账户，请使用游客登录",type:'error'})
        }
        })
    },
    anonymousLogin(){
      router.push('/home')
      this.$message({message:'游客浏览模式',type:'success'})
    },
  }
}
</script>

<style scoped lang="scss">
.login{
  width: 100%;
  height: 100%;
  position: absolute;
  background: url('https://oss.wenanguan.net/wenanguan/2021/11/%E7%8C%AB%E7%BE%BD%E9%9B%AB-%E8%93%9D%E8%89%B2%E7%9C%BC%E7%9D%9B%E5%A5%B3%E5%AD%90-%E5%B0%BE%E5%B7%B4-%E5%A5%B3%E5%AD%A9%E5%AD%90-%E7%8C%AB%E8%80%B3-e-%E5%8F%AF%E7%88%B1%E4%BA%8C%E6%AC%A1%E5%85%83%E5%8A%A8%E6%BC%AB4k%E5%A3%81%E7%BA%B8-scaled.jpg')  center  no-repeat ;
  background-size:100% 100%;
  -moz-background-size:100% 100%;
  .el-card{
    background: #65768557;
  }
  .box-card {
    width: 450px;
    margin: 200px auto;
    color: #fff;
    .el-form .el-form-item__label{
      color: #fff;
    }
    .el-card__header{
      font-size:34px;
    }
    .el-button{
      width: 100%;
    }
  }
}
</style>